// src/ChatWindow.tsx
import React, { useEffect, useRef, useState } from 'react';
import './ChatWindow.css';
import { Button, Spin } from 'antd';
import { SendOutlined, CloseOutlined } from '@ant-design/icons';
import useWebSocket, { ReadyState } from 'react-use-websocket';
import { init_chat } from '../api/init_chat';
import { WebSocketMessage } from '../type/types';

interface Message {
  id: string;
  text: string;
  isUser?: boolean;
}

const ChatWindow: React.FC = () => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputValue, setInputValue] = useState<string>('');
  const [isInitialLoading, setIsInitialLoading] = useState(true);
  const [chatId, setChatId] = useState<string | null>(null);
  const [socketUrl, setSocketUrl] = useState<string | undefined>(undefined);
  const processedMessageIds = useRef<Set<string>>(new Set());
  const chatContainerRef = useRef<HTMLDivElement>(null);

  //  
  useEffect(() => {
    const initializeChat = async () => {
      try {
        console.log('  ...');
        const response = await init_chat('a8b0496e-fc2b-49a4-bf2b-8108c90b8fe0');
        console.log('  :', response);
        setChatId(response.data.chat_id);
      } catch (error) {
        console.error('   :', error);
        setMessages([{ id: Date.now().toString(), text: '  ' }]);
        setIsInitialLoading(false);
      }
    };

    initializeChat();
  }, []);

  //  WebSocket URL   chatId
  useEffect(() => {
    if (chatId) {
      console.log(' WebSocket URL  chatId:', chatId);
      setSocketUrl(`wss://api-ai.deeptalk.tech/chat-server-ws/ws/${chatId}`);
      processedMessageIds.current.clear();
    }
  }, [chatId]);

  //  WebSocket
  const { sendJsonMessage, lastJsonMessage, readyState } = useWebSocket<WebSocketMessage>(
    socketUrl || null,
    {
      onOpen: () => {
        console.log('WebSocket ');
        setIsInitialLoading(false);
      },
      onClose: () => console.log('WebSocket '),
      onError: (error) => {
        console.error(' WebSocket:', error);
        setIsInitialLoading(false);
      },
      shouldReconnect: () => true,
      reconnectAttempts: 3,
      reconnectInterval: 500,
    },
    !!socketUrl
  );

  //   
  useEffect(() => {
    if (lastJsonMessage && lastJsonMessage.command === 'send_message' && lastJsonMessage.body?.message_id) {
      if (!processedMessageIds.current.has(lastJsonMessage.body.message_id)) {
        processedMessageIds.current.add(lastJsonMessage.body.message_id);
        const newMessage = {
          id: lastJsonMessage.body.message_id,
          text: lastJsonMessage.body.message?.content.text || ':   ',
        };
        setMessages((prev) => [...prev, newMessage]);
        setIsInitialLoading(false);
      }
    }
  }, [lastJsonMessage]);

  //  "start"  
  useEffect(() => {
    if (readyState === ReadyState.OPEN) {
      sendJsonMessage({
        command: 'send_message',
        body: {
          text: 'start',
        },
      });
    }
  }, [readyState, sendJsonMessage]);

  //  
  const sendMessage = () => {
    if (inputValue.trim() && readyState === ReadyState.OPEN) {
      const userMessage = {
        id: Date.now().toString(),
        text: inputValue,
        isUser: true,
      };
      setMessages((prev) => [...prev, userMessage]);
      sendJsonMessage({
        command: 'send_message',
        body: {
          text: inputValue,
        },
      });
      setInputValue('');
      scrollToBottom();
    }
  };

  //  
  const scrollToBottom = () => {
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTo({
        top: chatContainerRef.current.scrollHeight,
        behavior: 'smooth',
      });
    }
  };

  //   ()
  const closeChat = () => {
    setMessages([]);
    setChatId(null);
    setSocketUrl(undefined);
  };

  return (
    <div className="chat-container">
      <Button
        type="text"
        icon={<CloseOutlined />}
        onClick={closeChat}
        className="close-button"
      />
      <div ref={chatContainerRef} className="messages-container">
        {isInitialLoading && (
          <div className="loading-container" style={{ textAlign: 'center', padding: '20px' }}>
            <Spin />
            <div style={{ marginTop: '8px', color: '#666' }}>  ...</div>
          </div>
        )}
        {messages.map((message) => (
          <div
            key={message.id}
            className={`message ${message.isUser ? 'user-message' : 'other-message'}`}
          >
            <span className="text">{message.text}</span>
          </div>
        ))}
      </div>
      <div className="input-container">
        <input
          type="text"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          placeholder=" "
          disabled={isInitialLoading}
          className="input-field"
        />
        <Button
          icon={<SendOutlined />}
          onClick={sendMessage}
          disabled={isInitialLoading}
          className="send-button"
        />
      </div>
    </div>
  );
};

export default ChatWindow;